<template>
  <div
    class="dash-container"
    :class="{ night: nightMode }"
    v-bind:style="{ transform: 'scale(' + scaling + ')' }"
  >
    <div
      class="video-wrapper"
      :class="{
        full: camFullMode,
        enterAnimation: camFullMode,
        exitAnimation: !camFullMode,
      }"
    >
      <!-- <hls-video
        class="video-stream"
        v-if="stream"
        :video-src="streamUri + '/' + stream.streamUri"
      /> -->
      <cam-viewer
        class="video-stream"
        @enter="handleEnterFullStream()"
        @exit="handleExitFullStream()"
        :camSources="streams"
        :fullScreen="camFullMode"
        :streamUri="streamUri"
      ></cam-viewer>
      <div class="overlay1 overlay"></div>
      <div class="overlay2 overlay"></div>
    </div>
    <div
      v-if="currentTime.hr > -1"
      class="dash-overlay"
      :class="{ hide: camFullMode }"
    >
      <div class="current-temp-container">
        {{ weatherData ? Math.round(weatherData.current.temp) : "" }}
      </div>
      <div class="content-wrapper">
        <div class="date-container">{{ currentDay }}</div>
        <div class="time-container">
          {{ currentTime.hr
          }}<span class="separator" :class="{ tick: currentTime.tick }">:</span
          >{{ (currentTime.min < 10 ? "0" : "") + currentTime.min }}
          {{ currentTime.am ? "AM" : "PM" }}
        </div>
        <div class="day-temp-container">
          <div class="temp-high">
            {{ weatherData ? Math.round(weatherData.daily[0].temp.max) : "" }}
          </div>
          <div class="temp-low">
            {{ weatherData ? Math.round(weatherData.daily[0].temp.min) : "" }}
          </div>
        </div>
        <div class="forecast-container">
          <div
            class="forecast-item"
            v-for="(item, i) in weatherData
              ? weatherData.daily.slice(1, 6)
              : []"
            :key="i"
          >
            <span class="forecast-item-day">{{ getFriendlyDay(item.dt) }}</span>

            <img :src="getImageUrl(item.weather[0].icon + '@2x.png')" />
            <span class="forecast-item-temp"
              >{{ Math.round(item.temp.max) }}</span
            >
          </div>
        </div>
      </div>

      <div class="hero-condition-container">
        <img :src="getHeroConditionsUrl()" alt="" />
      </div>
    </div>
    <!--  svg masks-->

    <!-- main clip mask -->
    <svg
      width="0"
      height="0"
      viewBox="0 0 854 494"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <defs>
        <clipPath id="clip-path">
          <path id="target-clip" :d="clipPathDefinition" fill="#C4C4C4" />
        </clipPath>
      </defs>
    </svg>

    <!-- mask morph 1 -->
    <svg width="0" height="0" viewBox="0 0 854 494">
      <path
        id="clip-morph-1"
        d="M75.4829 376.618C22.4246 328.954 74.5838 119.176 137.289 102.36C199.994 85.5434 400.936 228.91 358.752 299.596C316.569 370.282 128.541 424.282 75.4829 376.618Z"
        fill="#C4C4C4"
      />
    </svg>

    <!-- mask morph 2 -->
    <svg width="0" height="0" viewBox="0 0 854 494">
      <path
        id="clip-morph-2"
        d="M92.9999 363.5C39.9415 315.836 90.4999 101.5 164.5 101.5C238.5 101.5 380.747 174.404 365.499 267.5C350.252 360.596 146.058 411.164 92.9999 363.5Z"
        fill="#C4C4C4"
      />
    </svg>

    <!-- mask morph 3 -->
    <svg width="0" height="0" viewBox="0 0 854 494">
      <path
        id="clip-morph-3"
        d="M60.0002 369.5C6.9418 321.836 136 91 210 91C284 91 391.248 239.904 376 333C360.752 426.096 113.059 417.164 60.0002 369.5Z"
        fill="#C4C4C4"
      />
    </svg>

    <!-- mask full expand -->
    <svg width="0" height="0" viewBox="0 0 854 494">
      <path id="clip-expand" d="M 0 0 H 854 V 494 H -854 Z" fill="#C4C4C4" />
    </svg>
  </div>
</template>


<script>
import { interpolate } from "polymorph-js";
import anime from "animejs/lib/anime.es.js";
import dayjs from "dayjs";
import ls from "localforage";
import CamViewer from "../components/CamViewer.vue";

export default {
  components: {
    CamViewer,
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      getListURI: "http://" + domain + ":" + httpPort + "/list",
      stopURI: "http://" + domain + ":" + httpPort + "/stop",
      startURI: "http://" + domain + ":" + httpPort + "/start",
      streamUri: "http://" + domain + ":" + httpPort,
      clipPathDefinition: "",
      streams: null,
      stream: null,
      morphInterpolator: null,
      fixedWidth: 854,
      fixedHeight: 494,
      scaleRatio: 1,
      offset: { x: 0, y: 0 },
      animator: null,
      camFullMode: false,
      fullClipD: "M 0 0 H 854 V 494 H -854 Z",
      weatherData: null,
      currentTime: {
        hr: -1,
        min: -1,
        am: false,
        tick: false,
      },
      currentDay: null,
      scaling: 1,
      safariHack: true,
      timer: null,
      weatherRateLimitMin: 30,
      autoReloadMin: 60 * 3,
      nightMode: false,
    };
  },
  methods: {
    handleEnterFullStream() {
      console.log("enter");

      this.enterFullView();
    },
    handleExitFullStream() {
      console.log("exit");
      this.exitFullView();
    },
    applyScaling() {
      var viewportW = window.innerWidth;

      if (viewportW > 1000) {
        var targetScaling = viewportW / this.fixedWidth;
        this.scaling = targetScaling;
      } else {
        this.scaling = 1;
        return false;
      }
    },
    getHeroConditionsUrl(code) {
      var conditionCode;
      var pathPrefix = "/images/conditions/";
      var getPath = (c) => {
        return pathPrefix + c + (this.nightMode ? "n" : "d") + ".png";
      };
      var url;
      if (this.weatherData) {
        conditionCode = this.weatherData.current.weather[0].id;
        if (conditionCode >= 200) url = getPath(200);
        if (conditionCode >= 300) url = getPath(300);
        if (conditionCode >= 600) url = getPath(600);
        if (conditionCode >= 701) url = getPath(701);
        if (conditionCode >= 800) url = getPath(800);
        if (conditionCode >= 801) url = getPath(801);
        // console.log(this.weatherData)
      }
      // console.log(url)

      if (url) return url;
    },
    getImageUrl(name) {
      // return require('../assets/conditions/'+name);
      return "/images/conditions/" + name;
    },
    async getStreams() {
      const response = await fetch(this.getListURI);
      return response.json();
    },
    exitFullView() {
      clearTimeout(this.timer);
      this.camFullMode = false;

      //exit
      this.clipPathDefinition = this.$el
        .querySelector("#clip-morph-1")
        .getAttribute("d");
      // p.setAttribute(
      //   "d",
      //   this.$el.querySelector("#clip-morph-1").getAttribute("d")
      // );
      var wrapperEl = this.$el.querySelector(".video-wrapper");
      document.body.style.setProperty(
        "--dash-bg-color",
        "var(--dash-bg-color-dash)"
      );
      this.timer = setTimeout(() => {
        this.startMaskMorphLoop();
      }, 1000);

      //safari hack
      if (this.safariHack) {
        wrapperEl.style.clipPath = "none";
        wrapperEl.offsetWidth; //force a style recalculation
        wrapperEl.style.clipPath = "url(#clip-path)";
      }
    },
    enterFullView() {
      // clearTimeout(this.timer);
      this.camFullMode = true;
      //enter
      this.animator.pause();
      this.clipPathDefinition = this.$el
        .querySelector("#clip-expand")
        .getAttribute("d");
      // p.setAttribute(
      //   "d",
      //   this.$el.querySelector("#clip-expand").getAttribute("d")
      // );
      var wrapperEl = this.$el.querySelector(".video-wrapper");
      document.body.style.setProperty(
        "--dash-bg-color",
        "var(--dash-bg-color-full)"
      );
      //auto revert
      // this.timer = setTimeout(() => {
      //   this.exitFullView();
      // }, 60000);

      //safari hack
      if (this.safariHack) {
        wrapperEl.style.clipPath = "none";
        wrapperEl.offsetWidth; //force a style recalculation
        wrapperEl.style.clipPath = "url(#clip-path)";
      }
    },
    toggleFullView() {
      // var p = this.$el.querySelector("#target-clip");
      if (this.camFullMode) {
        this.exitFullView();
      } else {
        this.enterFullView();
      }
    },
    getFriendlyDay(t) {
      return dayjs(new Date(t * 1000)).format("ddd");
    },
    startMaskMorphLoop() {
      // var morphTargets = ["#clip1", "#clip2", "#clip3"];
      var morphTargets = ["#clip-morph-1", "#clip-morph-2", "#clip-morph-3"];
      var morphStep = 0;

      var step = () => {
        var targetMorphIndex =
          morphStep >= morphTargets.length - 1 ? 0 : morphStep + 1;
        var targetMorph = morphTargets[targetMorphIndex];
        var interpolateParam = [morphTargets[morphStep], targetMorph];
        var morphInterpolator = interpolate(interpolateParam, {
          optimize: "none",
          precision: 4,
        });
        var animeTarget = {
          progress: 0,
        };
        this.clipPathDefinition = morphInterpolator(0);
        this.animator = anime({
          targets: animeTarget,
          progress: 1,
          easing: "easeInOutSine",
          duration: 3500,
          update: (anim) => {
            this.clipPathDefinition = morphInterpolator(animeTarget.progress);
            // console.log(morphInterpolator(animeTarget.progress))
            /**SAFARI HACK */
            if (this.safariHack) {
              var wrapperEl = this.$el.querySelector(".video-wrapper");
              wrapperEl.style.clipPath = "none";
              wrapperEl.offsetWidth; //force a style recalculation
              wrapperEl.style.clipPath = "url(#clip-path)";
            }
          },
          complete: () => {
            step();
          },
        });
        //increment
        morphStep++;
        if (morphStep >= morphTargets.length) {
          morphStep = 0;
        }
      };

      step();
    },
    async getWeatherData() {
      var weatherData;
      var cachedWeatherData;
      var refreshCache = true;
      //check local storage
      try {
        cachedWeatherData = await ls.getItem("weatherData");
      } catch (err) {
        console.log(err);
      }
      if (cachedWeatherData) {
        var cacheAgeSecond =
          (new Date().getTime() - cachedWeatherData.timeStamp) / 1000;
        if (cacheAgeSecond < 60 * this.weatherRateLimitMin) {
          //keep cache if less than 5 min old
          refreshCache = false;
          weatherData = cachedWeatherData.data;
          // console.log("using cached data...");
        }
      }

      if (refreshCache) {
        console.log("refreshing data...");
        //geocode
        var res;

        res = await fetch(
          "http://api.openweathermap.org/geo/1.0/direct?q=98005&limit=1&appid=47509517c71df0952467f060cb5c2528"
        );
        var geoData = await res.json();

        res = await fetch(
          "https://api.openweathermap.org/data/2.5/onecall?lat=" +
          geoData[0].lat +
          "&lon=" +
          geoData[0].lon +
          "&appid=47509517c71df0952467f060cb5c2528&units=imperial"
        );
        weatherData = await res.json();
        try {
          await ls.setItem("weatherData", {
            data: weatherData,
            timeStamp: new Date().getTime(),
          });
        } catch (err) {
          console.log(err);
        }
      }

      return new Promise((resolve, reject) => {
        resolve(weatherData);
      });
    },
    startClock() {
      var tick = () => {
        this.currentTime.tick = !this.currentTime.tick;
        // console.log(this.currentTime);
        var now = dayjs();
        this.currentTime.hr = now.hour() > 12 ? now.hour() % 12 : now.hour();
        if (this.currentTime.hr == 0) this.currentTime.hr = 12;

        this.currentTime.min = now.minute();
        this.currentTime.am = now.hour() < 12;
        this.currentDay = now.format("dddd DD/MM/YY");
        this.getWeatherData().then((data) => {
          this.weatherData = data;
        });
        // tick();
        // console.log(this.currentTime)
        var now = new Date().getTime();
        this.nightMode = now >= this.weatherData.daily[0].sunset * 1000 || now < this.weatherData.daily[0].sunrise * 1000;
        if (this.nightMode) {
          document.body.style.setProperty("--dash-bg-color-dash", "var(--dash-bg-color-night)");
        } else {
          document.body.style.setProperty("--dash-bg-color-dash", "var(--dash-bg-color-day)");
        }
        setTimeout(() => {
          tick();
        }, 1000);
      };

      tick();
    },
  },
  async mounted() {
    var res = await window.detectHomeNetwork();

    if (res) {
      this.getListURI = "http://" + window.destinations.localDomain + ":" + window.destinations.localPort + "/list";
      this.streamUri = "http://" + window.destinations.localDomain + ":" + window.destinations.localPort;
      console.log('home network')
    } else {
      this.getListURI = "http://" + window.destinations.remoteDomain + ":" + window.destinations.remotePort + "/list";
      this.streamUri = "http://" + window.destinations.remoteDomain + ":" + window.destinations.remotePort;
      console.log('remote network')
    };
    
    //
    this.applyScaling();
    window.onresize = () => {
      this.applyScaling();
    };

    this.streams = await this.getStreams();
    console.log(this.streams)
    this.stream = this.streams[2];
    this.weatherData = await this.getWeatherData();
    // console.log(this.weatherData);
    // console.log(dayjs)
    this.startClock();
    this.startMaskMorphLoop();
    //auto reload
    var autoReload = () => {
      setTimeout(() => {
        if (!this.camFullMode) {
          location.reload();
        } else {
          setTimeout(() => {
            autoReload();
          }, 1000 * 30);
        }
      }, this.autoReloadMin * 60 * 1000);
    };
    autoReload();
  },
};
</script>
